$bg-color: #f6f6f6;

// global settings
body {
    background-color: $bg-color;
}

ul li {
    list-style-type: none;
}

a:hover {
    text-decoration: none;
}

.content {
    margin-top: 115px;
    margin-bottom: 50px;
}
h2 a {
    color: #000000;
}

// login page
.login-content {
    display: table;
    position: fixed;
    width: 100%;
    height: 100%;
    background: url("../img/background.svg") no-repeat;
    background-size: cover;
}

.login-block {
    display: table-cell;
    vertical-align: middle;
}

.logo {
    user-select: none;

    img {
        display: block;
        margin: 0 auto;
    }

    h1 {
        text-align: center;
    }

    p {
        text-align: center;
        font-weight: bold;
    }
}

.jumbotron {
    background-color: rgba($color: #e9ecef, $alpha: .7);
}

.login-form {
    padding: 20px 20px 42px 20px;
    background-color: rgba($color: #e9ecef, $alpha: .7);

    &-header {
        text-align: center;
        margin: 15px 0;
        font-weight: 400;
    }

    .btn {
        display: block;
        width: 100%;
        margin: 37px auto 0 auto;
        border: 1px solid #212528;
        border-radius: 0;
        text-transform: uppercase;

    }
}

.social-link {
    text-align: center;
    margin: 16px 0;
    user-select: none;

    i {
        margin: 0 10px;
        font-size: 27px;
        color: #212528;
        cursor: pointer;
    }
}

// home page

.left-bar {
    position: fixed;
    margin-top: 25px;

    ul {
        margin-top: 20px;
    }

    li {
        margin: 10px 0;

        &:hover {
            background-color: #f7f7f7;
        }
    }

    a {
        font-size: 20px;
        font-weight: bold;
        color: rgba($color: #252525, $alpha: .68);

        i {
            margin-right: 5px;
            width: 20px;
        }

        &.active-page {
            color: #000000;
        }

    }
}

.menu {
    position: fixed;
    width: 20%;
    top: 150px;
    left: 0;
    z-index: 50;
}
.menu::before {
    content: '';
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    background-color: $bg-color;
    z-index: 2;
}
.menu ul {
    position: relative;
    z-index: 3;
}
.burger-menu {
    display: none;
}

.top-fixed {
    position: fixed;
    top: 0;
    right: 0;
    left: 25%;
    background-color: $bg-color;
    z-index: 10;
    margin-bottom: 10px;
}

.top-bar {
    display: flex;
    align-items: flex-start;
    flex-wrap: nowrap;
    margin-top: 25px;

    .search {
        width: 90%;

        input {
            width: 95%;
            padding: 10px;
            border: none;
            outline: none;
            background-color: rgba(233, 236, 239, 0.7);
        }
    }

    .user p:first-of-type {
        margin-right: 50px;
        font-size: 20px;
        font-weight: bold;
        color: rgba($color: #252525, $alpha: .68);

        img {
            width: 40px;
        }
    }
}

.content-block {
    margin: 50px 0;
}

// section:first-of-type {
//     margin-top: 70px;
// }


// player 

.player {
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 5px;
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: #ffffff;
    color: rgba($color: #252525, $alpha: .68);

    &-left {
        display: flex;
        flex: 1;
        align-items: center;
    }

    &-right {
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: flex-end;

        i {
            font-size: 20px;
            margin-left: 20px;
        }

        .dropdown-toggle::after {
            display: none;
        }
    }

    i {
        cursor: pointer;

    }

    .fa-fast-backward,
    .fa-fast-forward {
        position: relative;
        top: -8px;
        font-size: 30px;
    }

    .fa-play-circle {
        font-size: 50px;
        margin: 0 20px;
    }

}


// list 
.back-mob {
    display: none;
}

.list-info {
    margin-top: 100px;
    display: flex;

    .info {
        align-self: flex-end;
        margin-left: 5px;
    }

    img {
        width: 264px;
        height: 264px;
        margin-left: -14px;
    }


}

.track-container {
    margin-top: 50px;
    color: rgba($color: #252525, $alpha: 0.68);

    ul {
        margin: 0;
        padding: 0;

        li {
            border-top: 1px solid rgba($color: #252525, $alpha: 0.4);
            padding: 10px 0;

            h3 {
                font-size: 20px;
                font-weight: normal;
            }

            &:last-of-type {
                margin-bottom: 70px;
                border-bottom: 1px solid rgba($color: #252525, $alpha: 0.4);
            }
        }
    }

    .list-item {
        display: flex;

        .fa.fa-play-circle {
            font-size: 30px;
            margin: 0 10px;
        }
    }
}


// albums 

.albums {
    img {
        display: block;
    }

    p {
        text-align: left;
    }
}







//slick

.slick-slider:focus {
    outline: none;
}

.slick-slide {
    text-align: center;

    img {
        display: block;
        height: 264px;
        width: 264px;
        margin: 10px auto;
    }
}

.slick-prev {
    left: 89%;
    top: -10%;

    &::before {
        content: '\f0a8';
        font-family: "FontAwesome";
        color: #000;
        font-size: 30px;
    }
}

.slick-next {
    transform: rotate(180deg);
    left: 93%;
    top: -35px;

    &::before {
        content: '\f0a8';
        font-family: "FontAwesome";
        color: #000;
        font-size: 30px;
    }
}

a:-webkit-any-link:focus {
    outline: none;
}

:focus {
    outline: none;
}


//media requests

@media(max-width: 880px) {
    .slick-prev {
        left: 88%;
    }
}
@media (max-width: 685px) {
    .slick-prev {
        left: 87%;
    }    
}


@media (min-width: 992px) {
    .login-form {
        padding-bottom: 69px;
    }

}

@media (max-width: 991px) {
    content.lock {
        overflow: hidden;
    }
    .back-mob {
        display: block;
        i {
            color: #f6f6f6;
            width: 50px;
            height: 46px;
            position: absolute;
            top: -7%;
            z-index: 10;
            left: 8%;
        }
    }
    .back-mob-playlist {
        i {
            top: -90px;
            width: 63px;
            height: 58px;
            left: 7%;
        }
    }
    .top-bar {

        .user {
            p:first-of-type {
                margin-right: 20px;
                font-size: 17px;
            }
        }

        .search::before {
            content: '';
            clear: both;
            position: absolute;
            top: 0px;
            left: -33%;
            background-image: url('../img/icons8-пластинка-64.png');
            background-size: 50px 50px;
            background-position: center center;
            background-repeat: no-repeat;
            background-color: #f6f6f6;
            width: 33%;
            height: 91px;
        }
    }
    .list-info {
        margin-top: 20px;
    }
    .list-item {
        .dropdown-menu {
            width: 220px !important;
            transform: translate3d(-69px, -166px, 0px) !important;

            .dropdown-item {
                white-space: normal;
                margin-left: 12px;

                &:first-of-type::before,
                &:nth-child(2)::before,
                &:last-of-type::before {
                    content: '';
                    clear: both;
                    display: block;
                    position: absolute;
                    top: 6%;
                    left: 0;
                    background-image: url('../img/icons8-следующий-100.png');
                    background-size: cover;
                    background-repeat: no-repeat;
                    width: 30px;
                    height: 30px;
                }

                &:nth-child(2)::before {
                    top: 34%;
                    background-image: url('../img/icons8-плюс-64.png');
                    background-size: 25px 25px;
                    background-position: center center;
                }

                &:last-of-type::before {
                    top: 70%;
                    background-image: url('../img/icons8-heart-plus-50.png');
                    background-size: 25px 25px;
                    background-position: center center;
                }
            }
        }
    }


    .burger-menu {
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        
        span {
            background-color: #252525;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            top: 10px;
            transition: all 0.3s ease;
        }
        &::before,
        &::after {
            content: '';
            background-color: #252525;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            transition: all 0.3s ease;
        }
        &::before {
            top: 0;
            height: 4px;
        }
        &::after {
            bottom: 0;
        }
    }
    .burger-menu.active::before {
        transform: rotate(45deg);
        top: 10px;
        height: 2px;
    }
    .burger-menu.active::after {
        transform: rotate(-45deg);
        bottom: 7px;
    }
    .burger-menu.active span {
        transform: scale(0);
    }
    .left-bar .menu {
        position: fixed;
        left: -100%;
        z-index: 1020;
        top: 120px;
        width: 25%;
        height: 100%;
        transition: all 0.3 ease 0s;
        background-color: #184295;
        ul li {
            margin-bottom: 30px;
        
            a {
                color: #fff;
            }
            &:hover {
                background-color: #aa3401
            }
        }
        
    }   
    .menu::before {
        background-color: #aa3401;

    }
    .menu.active {
        left: 0;
    }
    .content{
        transition: all 0.3 ease 0s;
        position: relative;
        left: 0;
    }
    .content.active {
        position: relative;
        left: 300px;
    }

    .logo h1 {
        display: none;
    }
}

@media (min-width: 1200px) {
    .login-form {
        padding-bottom: 45px;
    }

}

@media (max-width: 1200px) {
    .left-bar {
        ul li a {
            font-size: 16px;
        }
        

        .logo {
            h1 {
                font-size: 40px;
            }
        }
    }
    .slick-slide.slick-cloned:nth-of-type(3) {
        visibility: hidden;
    }
}
@media (max-width: 576px) {
    .logo {
        display: none;
    }
    .slick-prev {
        left: 85%;
    }
    .left-bar .menu {
        width: 30%;
    }
    .burger-menu {
        display: block;
        position: relative;
        top: 65px;
        width: 30px;
        height: 20px;
        &::before {
            top: 2px;
            height: 2px;
        }
    }

    .top-bar {

        .user {
            p:first-of-type {
                margin-right: 20px;
                font-size: 17px;
            }
        }

        .search::before {
            content: '';
            clear: both;
            position: absolute;
            top: 0px;
            left: -33%;
            background-image: url('../img/icons8-пластинка-64.png');
            background-size: 50px 50px;
            background-position: center center;
            background-repeat: no-repeat;
            background-color: #f6f6f6;
            width: 33%;
            height: 91px;
        }
    }

    .list-info {
        img {
            height: 164px;
            width: 164px;
            margin-left: 0;
        }
    }

    .track-container {
        ul {
            li {
                &:last-of-type {
                    margin-bottom: 50px;
                }
            }
        }
    }

    .player {
        height:75px;
        .player-left {
            // margin-right: 4px;
            h3 {
                font-size: 18px;

            }

        }


        .player-control {
            padding: 15px 5px;

            .fa-fast-backward,
            .fa-fast-forward {
                position: relative;
                top: 0;
                font-size: 25px;
            }

            .fa-play-circle {
                font-size: 28px;
                margin: 0 8px;
            }

        }
        .player-right {
            // flex: 0;
            display: none;
            visibility: hidden;
            .fa-thumbs-o-up,
            .fa-thumbs-o-down {
                display: none;
            }
        }

    }
}
@media (max-width: 500px) {
    .slick-prev {
        left: 83%;
    }

}
@media (max-width: 400px) {
    .slick-prev {
        left: 83%;
    }
}


.active_a  {
    color: #000 !important;
}